// Structure

$base__space-unit: 1.5rem;
$font__size: 1.25rem;
$font__Pweight: 300; 
$font__Hweight: 100;



// Colors


$color__background: #222;
$color__mainfont: #fff;
$color__link: #0c0;




// Typography

$font__main: sans-serif;
$font__code: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
$font__pre: "Courier 10 Pitch", Courier, monospace;
$font__line-height-body: 1.5;
$font__line-height-pre: 1.6;

$fontface__main: 'exo', sans-serif;



//border

$border__dash: 1px dashed #ffffff;
$border_button: solid 2px $color__link;







